body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: whitesmoke;
}

a {
  font-weight: bolder;
  text-shadow: 1px 1px 1px;
  font-size: 25px;
}

.brandLook {
  color: whitesmoke;
}

.brandLook:hover {
  text-decoration: none;
  color: white;
  font-size: 26px;
}

.dropLook {
  color: #333;
  font-weight: bolder;
  font-size: 20px;
}

.dropLook:hover {
  text-decoration: none;
  color: dodgerblue;
}

.quiz-look {
  margin-top: 30px;
  border: 2pz solid #333;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 10px 10px 10px #333;
}

h5,
h3,
h2 {
  text-align: center;
  font-weight: bolder;
  text-shadow: 5px 5px 10px #333;
}

.imgLook {
  max-height: 300px;
  border-radius: 5px;
  box-shadow: 10px 10px 10px #333;
}

.dicesLook {
  margin-top: 20px;
}

.movieLook1 {
  max-height: 300px;
  margin-top: 20px;
  margin-left: 30px;
  margin-bottom: 20px;
  box-shadow: 10px 10px 10px #333;
  border-radius: 10px;
}

.headingLook {
  text-align: center;
  margin-top: -100px;
  margin-left: -40px;
  color: black;
  font-weight: bolder;
  text-shadow: 5px 5px 10px white;
}
.carLook1 {
  box-shadow: 10px 10px 10px #333;
}

@media screen and (max-width: 768px) {
  .movieLook1 {
    max-height: 200px;
    margin-left: 20px;
  }
  .headingLook {
    margin-left: 0px;
  }
}

