    padding: 5px;
    margin-top: 150px;
    background-color: whitesmoke;
    width: 30vw;
  }
  .imageLook {
    margin-top: -350px;
    width: 60%;
    float: right;
    box-shadow: 5px 5px 10px;
  }
  .answersLook {
    background-image: url(/fun/static/media/smile.f5298302.jpg);
    background-position: right;
    background-repeat: no-repeat;
    margin-top: 80px;
    margin-left: 50px;
    border: 3px solid #333;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    padding: 15px;
    width: 30%;
    box-shadow: 5px 5px 5px;
  }
  .correctLook {
    text-align: center;
    margin-right: 50%;
    font-weight: bolder;
    text-shadow: 5px 5px 5px #333;
