.playerLook,
.buttonLook {
  padding-left: 10px;
  padding-right: 20px;
  border: 2px solid #333;
  border-radius: 15px;
  margin-top: 2px;
  font-weight: bolder;
}

.butLook {
  width: 90%;
  margin-left: 5%;
  margin-right: 5%;
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: bolder;
}

.player-score {
  text-align: center;
  margin-top: 50px;
  margin-right: 50px;
  font-size: 50px;
  font-weight: bolder;
  text-shadow: 3px 3px 5px;
}

.player-box {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  margin-top: 10px;
  background: dodgerblue;
  color: white;
  text-align: center;
  font-weight: bolder;
  box-shadow: 3px 3px 5px dodgerblue;
  padding: 2px;
}

.player-current-score {
  font-size: 20px;
}

.diceLook img {
  width: 50%;
  margin-left: 25%;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 20px;
  border: 2px solid #333;
  box-shadow: 3px 3px 6px #333;
  border-radius: 5px;
}

.activePlayer {
  background: grey;
}

.recomendedMovies {
  float: left;
  max-width: 300px;
  margin-left: 30px;
  margin-top: 10px;
  background-image: url(/fun/static/media/film1.7bc1f89d.jpg);
  padding: 10px;
  border: 3px solid #333;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  box-shadow: 5px 5px 12px #333;
  font-weight: bolder;
}

.moviesLook {
  max-width: 100%;
  margin-left: 100%;
}

.movieLooking {
  background-image: url(/fun/static/media/film1.7bc1f89d.jpg);
}

button {
  box-shadow: 5px 5px 10px dodgerblue;
}

.addingLook {
  float: left;
  width: 40%;
  margin-left: 30px;
  margin-top: -400px;
  padding: 10px;
  border: 3px solid #333;
  border-radius: 20px;
  box-shadow: 5px 5px 12px #333;
  font-weight: bolder;
  background: whitesmoke;
}

label {
  padding-left: 30px;
  padding-right: 50px;
}

input,
select {
  width: 300px;
}

.favoriteList {
  float: left;
  width: 55%;
  margin-left: 600px;
  margin-top: -340px;
  padding: 10px;
  border: 3px solid #333;
  border-radius: 20px;
  box-shadow: 5px 5px 12px #333;
  font-weight: bolder;
  background: whitesmoke;
}

.gridLook {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.actorStyle {
  margin-left: 12px;
}
.yearStyle {
  margin-left: -26px;
}
.genreStyle {
  margin-left: 44px;
}
.favouriteStyle {
  background-color: dodgerblue;
  color: whitesmoke;
  margin-left: 20px;
}

@media screen and (max-width: 768px) {
  .recomendedMovies {
    margin-left: 5px;
  }
  .gridLook {
    width: 95vw;
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .favoriteList {
    width: 95vw;
    display: grid;
    grid-template-columns: 1fr;
    margin-left: 0;
    margin-top: 10px;
  }
  .addingLook {
    width: 95vw;
    display: grid;
    grid-template-columns: 1fr;
    margin-left: 0;
    margin-top: 10px;
  }
  .moviesLook {
    max-width: 0;
    margin-left: 0;
  }
  .actorStyle {
    margin-left: 0;
  }
  .yearStyle {
    margin-left: 0;
  }
  .genreStyle {
    margin-left: 0;
  }
  .favouriteStyle {
    margin-left: 0;
  }
}

.carLook {
  margin-top: 80px;
  background: lightgrey;
  border-radius: 0.8rem;
  padding-bottom: 5px;
}

.carLook img {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
}

.carLook h3 {
  text-transform: capitalize;
  text-align: center;
  font-weight: bolder;
}

.carLook p {
  padding: 5px;
  text-align: justify;
}

.carLook h5 {
  width: 30%;
  margin-left: 35%;
  margin-right: 35%;
  text-align: center;
  border: 2px solid #333;
  border-radius: 10px;
  font-weight: bolder;
  cursor: pointer;
  box-shadow: 5px 5px 15px #333;
}
.carLook h5:hover {
  border: 4px solid #333;
}

.moreInfo {
  border: 2px solid #333;
  border-radius: 25px;
  margin-top: -15px;
  padding: 5px;
  box-shadow: 3px 3px 10px #333;
}

@media screen and (min-width: 769px) {
  .carLook {
    height: 600px;
  }
  .moreInfo {
    height: 650px;
  }
}

.carList {
  width: 90vw;
  margin: 20px auto 0;
  padding: 1rem;
}

@media screen and (min-width: 769px) {
  .carList {
    width: 95vw;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 1.5rem;
  }
}

.factsLook {
  background: lightgrey;
  margin: 30px 10px;
  padding: 10px;
  text-align: justify;
  border: 3px solid #333;
  border-radius: 10px;
  box-shadow: 20px 20px 40px #333;
}

.btn-journey {
  border: 5px solid #333;
  border-radius: 10px;
  color: #333;
  font-weight: bolder;
  text-align: center;
  margin-left: 20%;
  padding: 5px;
  cursor: pointer;
  box-shadow: 10px 10px 15px #333;
}
.btn-journey:hover {
  border: 7px solid #333;
}

.paragraph-style {
  float: left;
  width: 40%;
  margin-left: 10%;
  margin-top: 20px;
  background: lightgrey;
  border: 3px solid #333;
  border-radius: 15px;
  padding: 20px;
  font-weight: bolder;
  text-align: justify;
  box-shadow: 5px 5px 15px #333;
}
.image-style {
  float: left;
  width: 30%;
  margin-top: 15px;
  box-shadow: 15px 15px 25px #333;
}

@media screen and (max-width: 768px) {
  .paragraph-style {
    width: 80%;
  }
  .image-style {
    width: 90%;
    margin-left: 5%;
    margin-top: -20px;
  }
}

