.factsLook {
  background: lightgrey;
  margin: 30px 10px;
  padding: 10px;
  text-align: justify;
  border: 3px solid #333;
  border-radius: 10px;
  box-shadow: 20px 20px 40px #333;
}

.btn-journey {
  border: 5px solid #333;
  border-radius: 10px;
  color: #333;
  font-weight: bolder;
  text-align: center;
  margin-left: 20%;
  padding: 5px;
  cursor: pointer;
  box-shadow: 10px 10px 15px #333;
}
.btn-journey:hover {
  border: 7px solid #333;
}

.paragraph-style {
  float: left;
  width: 40%;
  margin-left: 10%;
  margin-top: 20px;
  background: lightgrey;
  border: 3px solid #333;
  border-radius: 15px;
  padding: 20px;
  font-weight: bolder;
  text-align: justify;
  box-shadow: 5px 5px 15px #333;
}
.image-style {
  float: left;
  width: 30%;
  margin-top: 15px;
  box-shadow: 15px 15px 25px #333;
}

@media screen and (max-width: 768px) {
  .paragraph-style {
    width: 80%;
  }
  .image-style {
    width: 90%;
    margin-left: 5%;
    margin-top: -20px;
  }
}
